import React, { Component } from 'react'

export class TodoItem extends Component {
    render() {
        return (
            <div>
                <label>
                <input type="checkbox" onChange={this.props.markComplete.bind(this)}/>
                {this.props.todo.title}
                </label>
            </div>
        )
    }
}

export default TodoItem



