import React, {Component} from 'react';

import InputComp from './InputComp';
import Todo from './Todo';
//import React from 'react';
//import ReactDOM from 'react-dom';

class ReacTrial extends Component{

  state = {
    todos : [
      {
        id : 1,
        title: "Task One",
        completed:true
      },
      {
        id : 2,
        title: "Task Two",
        completed:true

      },
      {
        id : 3,
        title: "Task Three",
        completed:true

      }


    ]
  }

  markComplete = this.markComplete.bind(this);

  markComplete(id){
  console.log(id);

   this.setState({

    todos:
    this.state.todos.map(todo => {
      if(todo.id === id)
      {
        todo.completed = !todo.completed
      }
      return todo;
    })

   })
    
  }


  render() {
    return (
    <div>
    <h1>React Learning</h1>
    <p>
    In this crash course you will learn 
    what React JS is and the fundamentals such as components, state, props, JSX, 
    events, etc. React Front To Back 2019 - 13.5 Hour Course
    In this crash course you will learn 
    what React JS is and the fundamentals such as components, state, props, JSX, 
    events, etc. React Front To Back 2019 - 13.5 Hour Course
    </p>
    <ul>
      <li>item1</li>
      <li>item2</li>
      <li>item3</li>
    </ul>
    <InputComp />
    <Todo todos={this.state.todos} markComplete={this.markComplete} />
  </div>
    )
}

}
export default ReacTrial ;


